import React from 'react';
import { useSelector } from 'react-redux';
import useFilledDiagnosis from '../../../../hooks/useFilledDiagnosis';
import useTriage from '../../../../hooks/useTriage';
import Smile from './Smile';

const SmileContainer = () => {
  const { name: userName } = useSelector(state => state.user);
  const { lastDate } = useFilledDiagnosis();
  const { isCovid, isExposure, riskGroup, IconComponent } = useTriage();

  const getContent = () => {
    if (isCovid) {
      return (
        <>
          Dzikujemy, e mylisz o innych! To odpowiedzialna i solidarna
          decyzja. Przekazana przez Ciebie informacja o ryzyku kontaktu trafi na
          urzdzenia osb, ktre mogy mie z Tob kontakt.
        </>
      );
    }
    if (isExposure) {
      return (
        <>
          <strong>
            Modu powiadamiania o moliwym kontakcie z koronawirusem
          </strong>{' '}
          przeanalizowa Twoje dane. Twj wynik to:&nbsp;
          <strong>{riskGroup}</strong>.
        </>
      );
    }
    return (
      <>
        przeanalizowalimy Twoje odpowiedzi. Wynik testu z&nbsp;
        <strong className="nowrap">{lastDate}</strong> kwalifikuje Ci do
        grupy:&nbsp;
        <strong>{riskGroup}</strong>.
      </>
    );
  };
  return (
    <Smile
      userName={userName}
      IconComponent={IconComponent}
      content={getContent()}
    />
  );
};

export default SmileContainer;
