import React from 'react';

import { InfoBorderContainer, Content } from './TriageInfoBorder.styled';
import useTriage from '../../../../hooks/useTriage';
import ArrowIcon from '../../../../assets/img/icons/angle-right.svg';
import { PhoneNumber } from '../../../../components';

const TriageInfoBorder = () => {
  const {
    isCovid = false,
    isExposure = false,
    color,
    description,
    serious = [],
    riskLevel
  } = useTriage();

  const goToAdvice = () => {
    window.open('https://pacjent.gov.pl/teleporada', '_blank');
  };

  const renderItem = serious
    .map(_obj => _obj.common_name)
    .map(item => <li key={item}>{item}</li>);

  const renderContent = () => {
    if (isCovid) {
      return (
        <>
          Bardzo le si czujesz? Nie czekaj. Zadzwo na numer alarmowy 112 lub
          999. Poinformuj o swojej chorobie.
        </>
      );
    }
    if (isExposure && riskLevel === 1) {
      return (
        <>
          Pamitaj, by regularnie my rce wod z mydem lub dezynfekowa donie
          oraz zachowywa dystans spoeczny.
        </>
      );
    }
    if (isExposure && riskLevel === 2) {
      return (
        <>
          Regularnie obserwuj swj stan zdrowia, mierz temperatur dwa razy
          dziennie i zapisuj j w Dzienniku Zdrowia.
        </>
      );
    }
    if (isExposure && riskLevel === 3) {
      return (
        <>
          W przypadku pojawienia si objaww takich jak: gorczka, kaszel lub
          duszno, skontaktuj si telefonicznie ze swoim lekarzem lub
          skorzystaj z darmowej teleporady z pracownikiem medycznym.
          <Content onClick={goToAdvice}>
            Skorzystaj z teleporady
            <img src={ArrowIcon} alt="button" />
          </Content>
          Twj stan jest ciki? Nie czekaj. Zadzwo na numer alarmowy{' '}
          <PhoneNumber>112</PhoneNumber> lub <PhoneNumber>999</PhoneNumber> i
          uprzed, e moesz mie koronawirusa.
        </>
      );
    }
    return (
      <>
        {description}
        &nbsp;Szczeglnie niepokojce objawy:
      </>
    );
  };

  return (
    <InfoBorderContainer color={color}>
      <span className="p1">{renderContent()}</span>
      {renderItem.length >= 1 && <ul>{renderItem}</ul>}
    </InfoBorderContainer>
  );
};

export default TriageInfoBorder;
