import React from 'react';
import './NotSupported.scss';
import {
  OnboardingContent,
  Button,
  GovFooter
} from '../../../../../../components';
import { isIOSWebView } from '../../../../../../utils/native';
import Header from '../../../../../../components/Header/Header';
import { View, Container, Content } from '../../../../../../theme/grid';
import { Icon } from './NotSupported.styled';

const NotSupported = ({ onNext }) => {
  const buttons = [
    {
      border: false,
      text: 'Rozumiem',
      onClick: onNext
    }
  ];

  const renderButton = buttons.map(button => (
    <Button
      key={button.text}
      onClick={button.onClick}
      text={button.text}
      border={button.border}
    />
  ));

  return (
    <View>
      <Header hideBackButton />
      <Content>
        <Container className="full-height">
          <OnboardingContent
            icon={<Icon />}
            title="Niezgodna wersja"
            buttons={renderButton}
          >
            {!isIOSWebView() ? (
              <p>
                Co jest nie tak. Sprawd swoje urzdzenie - zaktualizuj wersj
                systemu swojego telefonu. To konieczne, jeli chcesz w peni
                korzysta ze wszystkich funkcjonalnoci aplikacji. Wsparcie dla
                powiadamiania o moliwym kontakcie z koronawirusem posiadaj
                zaktualizowane urzdzenia Android.
              </p>
            ) : (
              <p>
                Wsparcie dla powiadamiania o moliwym kontakcie z koronawirusem
                posiadaj tylko urzdzenia iPhone z iOS 13.5 i nowszym. Sprawd
                swoje urzdzenie - zaktualizuj wersj systemu swojego telefonu.
                To konieczne, jeli chcesz w peni korzysta ze wszystkich
                funkcjonalnoci aplikacji.
              </p>
            )}
          </OnboardingContent>
          <GovFooter type="black" />
        </Container>
      </Content>
    </View>
  );
};

export default NotSupported;
